<template>
  <div class="pt50">
    <video-player
      class="video-player vjs-custom-skin"
      ref="videoPlayer"
      :playsinline="true"
      :options="playerOptions"
    ></video-player>
    <van-collapse v-model="activeNames" accordion>
      <van-collapse-item size="large" value="简介" name="1">
        <template #title>
          <div>
            迷之自信
            <span class="size12" style="color:rgb(255, 85, 51)">8.0</span>
            <span class="size12">分</span>
          </div>
        </template>
        <div class="title">演员：李现</div>
        <div class="mb5">设计师将图标上传到Iconfont平台，用户可以自定义下载多种格式的icon，平台也可将图标转换为字体，便于前端工程师自由调整与调用。</div>
      </van-collapse-item>
      <van-collapse-item title="精彩剧照" name="2">
        <img class="viewImg" v-for="(img,index) in imageList" :key="index" :src="img" />
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
import pic1 from "../../assets/shortVideo/1.jpg";
import pic2 from "../../assets/shortVideo/2.jpg";

export default {
  data() {
    return {
      playerOptions: {
        playbackRates: [0.5, 1.0, 1.5, 2.0], //播放速度
        autoplay: false, //如果true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: "zh-CN",
        aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [
          {
            type: "", //这里的种类支持很多种：基本视频格式、直播、流媒体等，具体可以参看git网址项目
            src:
              "https://storage.jd.com/about/big-final.mp4?Expires=3730193075&AccessKey=3LoYX1dQWa6ZXzQl&Signature=ViMFjz%2BOkBxS%2FY1rjtUVqbopbJI%3D" //url地址
          }
        ],
        poster: "/static/2.1.1.jpg", //你的封面地址
        // width: document.documentElement.clientWidth, //播放器宽度
        notSupportedMessage: "此视频暂无法播放，请稍后再试", //允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true //全屏按钮
        }
      },
      activeNames: ["1"],
      imageList: [pic1, pic2]
    };
  },
  methods: {}
};
</script>
<style lang="less" scoped>
.video-js .vjs-icon-placeholder {
  width: 100%;
  height: 100%;
  display: block;
}
.vjs-big-play-button {
  width: 100px !important;
  height: 60px !important;
}
.size12 {
  font-size: 12px;
}
.title {
  font-size: 14px;

  margin-bottom: 5px;
}
.viewImg {
  height: 150px;
  width: 100%;
  margin-bottom: 5px;
}
</style>



