<template>
  <div class="menu">
    <van-dropdown-menu active-color="rgb(255, 85, 51)">
      <van-dropdown-item v-model="value1" :options="option1" />
      <van-dropdown-item v-model="value2" :options="option2" />
      <van-dropdown-item v-model="value3" :options="option3" />
    </van-dropdown-menu>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value1: 0,
      value2: 0,
      value3: 0,
      option1: [
        { text: "全部", value: 0 },
        { text: "动作", value: 1 },
        { text: "喜剧", value: 2 },
        { text: "惊悚", value: 3 },
        { text: "谍战", value: 4 },
        { text: "古装", value: 5 },
        { text: "犯罪", value: 6 },
        { text: "其它", value: 7 }
      ],
      option2: [
        { text: "全部", value: 0 },
        { text: "内陆", value: 1 },
        { text: "港澳", value: 2 },
        { text: "美国", value: 3 },
        { text: "英国", value: 4 },
        { text: "日本", value: 5 },
        { text: "韩国", value: 6 },
        { text: "其它", value: 7 }
      ],
      option3: [
        { text: "全部", value: 0 },
        { text: "2020", value: 1 },
        { text: "2019", value: 2 },
        { text: "2018", value: 3 },
        { text: "2017", value: 4 },
        { text: "2016", value: 5 },
        { text: "2015", value: 6 },
        { text: "其它", value: 7 }
      ]
    };
  },
  methods: {}
};
</script>
<style lang='less' scoped>
.menu {
  background: linear-gradient(0deg, #f0ebeb, #ffffff);
  box-shadow: #999 1px 1px 5px;
}
</style>


