<template>
  <div class="m12 h50 toApp plr10">
    <img class="mr5" src="../../../../static/jianguo.png" alt />
    <div class="text">打开栗子，看高清视频</div>
    <van-button
      @click="toApp"
      size="small"
      color="linear-gradient(90deg, #1a2e4a 0, #409890 100%)"
    >打开看看</van-button>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    toApp() {
      this.$dialog
        .confirm({
          message: "你还未安装app，是否前往下载",
          confirmButtonColor: "rgb(255, 85, 51)"
        })
        .then(() => {
          this.$toast.success("下载成功");
        })
        .catch(() => {
          // on cancel
          this.$toast("已取消");
        });
    }
  }
};
</script>
<style lang='less' scoped>
.toApp {
  background: #cbedd6;
  img {
    width: 30px;
    height: 30px;
  }
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.text {
  flex-grow: 1;
  text-align: left;
  font-weight: 600;
  font-size: 14px;
}
</style>


