<template>
  <div>
    <van-swipe height="190" class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item @click="toDtl(item.id)" v-for="(item, index) in bannerList" :key="index">
        <img style="height:190px;" :src="item.url" alt />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bannerList: [
        {
          id: 1,
          path: "/",
          url: require("../../../assets/home/bg1.jpg")
        },
        {
          id: 2,
          path: "/",
          url: require("../../../assets/home/bg2.jpg")
        },
        {
          id: 3,
          path: "/",
          url: require("../../../assets/home/bg3.jpg")
        },
        {
          id: 4,
          path: "/",
          url: require("../../../assets/home/bg4.jpg")
        },
        {
          id: 5,
          path: "/",
          url: require("../../../assets/home/bg5.jpg")
        }
      ]
    };
  },
  methods: {
    toDtl(id) {
      this.$router.push("/dtl?id=" + id);
    }
  }
};
</script>
<style lang='less' scoped>
</style>


