import Vue from "vue";
import Router from "vue-router";
import {
  Toast,
  Dialog
} from "vant";
import store from "../store";
import NavBar from "../components/NavBar.vue";
import Tabbar from "../components/Tabbar.vue";
import register from "../view/register.vue";
import login from "../view/login.vue";
import forgetPassword from "../view/forgetPassword.vue";
import home from "../view/home/index.vue";
import moreList from "../view/home/moreList.vue";
import dtl from "../view/home/videoDtl.vue";
import shortVideo from "../view/shortVideo/index.vue";
import user from "../view/user/index.vue";
import NotFound from "../view/NotFound.vue";

Vue.use(Router);

let router = new Router({
  routes: [{
      path: "/",
      redirect: "/home"
    },
    {
      path: "/home",
      name: "home",
      components: {
        default: home,
        Tabbar: Tabbar
      }
    },
    {
      path: "/moreList",
      name: "moreList",
      meta: {
        title: "筛选页",
        isShow: true
      },
      components: {
        default: moreList,
        NavBar: NavBar
      }
    },
    {
      path: "/dtl",
      name: "dtl",
      meta: {
        title: "栗子视频",
        isShow: true
      },
      components: {
        default: dtl,
        NavBar: NavBar
      }
    },
    {
      path: "/shortVideo",
      name: "shortVideo",
      meta: {
        title: "短视频",
        isShow: false
      },
      components: {
        default: shortVideo,
        NavBar: NavBar,
        Tabbar: Tabbar
      }
    },
    {
      path: "/user",
      name: "user",
      meta: {
        title: "我的",
        isShow: false
      },
      components: {
        default: user,
        NavBar: NavBar,
        Tabbar: Tabbar
      }
    },
    {
      path: "/login",
      name: "login",
      components: {
        default: login,
        NavBar: NavBar
      },
      meta: {
        title: "登录",
        isShow: false
      },

    },
    {
      path: "/register",
      name: "register",
      components: {
        default: register,
        NavBar: NavBar
      },
      meta: {
        title: "注册",
        isShow: true
      },
    },
    {
      path: "/forgetPassword",
      name: "forgetPassword",
      components: {
        default: forgetPassword,
        NavBar: NavBar
      },
      meta: {
        title: "修改密码",
        isShow: true
      },
    },
    {
      path: "*",
      meta: {
        title: "404",
        isShow: true
      },
      components: {
        default: NotFound,
        NavBar: NavBar
      }
    }
  ]
});
router.beforeEach((to, from, next) => {
  if (
    to.path === "/forgetPassword" ||
    to.path === "/login" ||
    to.path === "/register" ||
    to.path === "/user" ||
    to.path === "/home" ||
    to.path === "/shortVideo"
  ) {
    next();
  } else {
    let token = store.getters.token;
    if (!token) {
      Dialog
        .confirm({
          message: "你还未登录，是否登录",
          confirmButtonColor: "rgb(255, 85, 51)"
        })
        .then(() => {
          next("/login");
        })
        .catch(() => {});
    } else {
      next();
    }
  }
});
export default router;



