<template>
  <div class="user pt50 pb50">
    <div class="headerpic mb5">
      <img src="../../assets/user/logo.gif" alt />
      <div style="color:#999">{{count}}</div>
    </div>
    <van-cell v-if="isLogin" class="mb5" title="会员到期时间" is-link value="2025/01/01" />
    <van-cell v-if="isLogin" title="播放记录" is-link value="记录看过的精彩" />
    <!-- 历史记录 -->
    <div v-if="isLogin" class="home_item mb5">
      <div class="view" @click="toDtl(item.id)" v-for="(item, index) in dmuijuList" :key="index">
        <img style="height:150px" :src="item.url" alt />
        <div class="box">
          <div class="title">{{ item.title }}</div>
          <div class="desc">{{ item.desc }}</div>
        </div>
      </div>
    </div>
    <van-cell title="关于版本" is-link value="1.0.0" />

    <div class="logoutBtn">
      <van-button v-if="isLogin" type="danger" @click="logOut" block>退出登录</van-button>
      <van-button v-else type="danger" @click="login" block>登录</van-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dmuijuList: [
        {
          id: "5",
          url: require("../../assets/home/3.1.1.jpg"),
          title: "生活就是这样",
          desc: "川建国同志"
        },
        {
          id: "6",
          url: require("../../assets/home/3.1.2.jpg"),
          title: "生活就是这样",
          desc: "川建国同志"
        },
        {
          id: "7",
          url: require("../../assets/home/3.1.3.gif"),
          title: "生活就是这样",
          desc: "川建国同志"
        },
        {
          id: "8",
          url: require("../../assets/home/dsj1.png"),
          title: "生活就是这样",
          desc: "川建国同志"
        },
        {
          id: "9",
          url: require("../../assets/home/dsj2.jpg"),
          title: "生活就是这样",
          desc: "川建国同志"
        },
        {
          id: "10",
          url: require("../../assets/home/dsj3.jpg"),
          title: "生活就是这样",
          desc: "川建国同志"
        }
      ],
      isLogin: this.$store.getters.token ? true : false,
      count: localStorage.getItem("userInfo")
        ? JSON.parse(localStorage.getItem("userInfo")).count
        : "暂未登录"
    };
  },
  methods: {
    toDtl(id) {
      this.$router.push("/dtl?id=" + id);
    },
    login() {
      this.$router.push("/login");
    },
    logOut() {
      this.$toast.loading({
        duration: 0,
        message: "加载中...",
        loadingType: "spinner",
        forbidClick: true
      });
      setTimeout(() => {
        this.$toast.clear();
        this.$toast.success("退出成功");
        localStorage.removeItem("userInfo");
        this.$store.commit("removeToken", "");
        this.$router.push("/home");
      }, 1000);
    }
  },
  created() {}
};
</script>

<style scoped lang="less">
.user {
  .headerpic {
    background: #fff;
    text-align: center;
    padding-bottom: 15px;

    img {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      margin-top: 20px;
      margin-bottom: 5px;
    }
  }
  .logoutBtn {
    margin: 30px 15px 15px;
  }
}
.home_item::-webkit-scrollbar {
  background-color: transparent;
}
.home_item {
  padding: 5px 12px;
  width: 100%;
  display: -webkit-box;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
  box-sizing: border-box;
  background: #fff;
  .view {
    width: 35%;
    margin-right: 10px;
  }
  img {
    width: 100%;
  }
  .box {
    padding: 2px 2px 10px;
    .title {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 14px;
      font-weight: 600;
      color: #222;
    }
    .desc {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #999;
      font-size: 12px;
    }
  }
}
</style>



