<template>
  <div class="header search_fixed">
    <div class="header_left search_ico">
      <img src="../../static/jianguo.png" alt />
    </div>
    <van-search
      class="header_right"
      v-model="value"
      background="linear-gradient(90deg, #1a2e4a 0, #409890 100%)"
      shape="round"
      placeholder="请输入搜索关键词"
      @search="onSearch"
    ></van-search>
    <div>
      <van-icon @click="toNavBar('/shortVideo')" name="fire-o" />
      <van-icon @click="toNavBar('/user')" class="mr12" name="gem-o" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: ""
    };
  },
  methods: {
    toNavBar(name) {
      this.$router.push(name);
    },
    onSearch() {
      this.$emit("Search", this.value);
    }
  }
};
</script>
<style lang='less' scoped>
.van-icon-fire-o,
.van-icon-gem-o {
  color: #fff;
  font-size: 22px;
  margin-top: 16px;
}
.search_fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}
.header {
  display: flex;
  background: #409890;

  .header_right {
    flex-grow: 1;
  }
}
.search_ico {
  height: 54px;
  line-height: 54px;
  background: #1a2e4a;
}
.search_ico img {
  width: 30px;
  height: 30px;
  margin-top: 12px;
  margin-left: 12px;
}
</style>


