<template>
  <div class="home_item">
    <van-row gutter="10">
      <van-col @click="toDtl(item.id)" span="8" v-for="(item, index) in dmuijuList" :key="index">
        <img style="height:150px" :src="item.url" alt />
        <div class="box">
          <div class="title">{{ item.title }}</div>
          <div class="desc">{{ item.desc }}</div>
        </div>
      </van-col>
    </van-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dmuijuList: [
        {
          id: "5",
          url: require("../assets/home/3.1.1.jpg"),
          title: "生活就是这样",
          desc: "川建国同志"
        },
        {
          id: "6",
          url: require("../assets/home/3.1.2.jpg"),
          title: "生活就是这样",
          desc: "川建国同志"
        },
        {
          id: "7",
          url: require("../assets/home/3.1.3.gif"),
          title: "生活就是这样",
          desc: "川建国同志"
        },
        {
          id: "8",
          url: require("../assets/home/dsj1.png"),
          title: "生活就是这样",
          desc: "川建国同志"
        },
        {
          id: "9",
          url: require("../assets/home/dsj2.jpg"),
          title: "生活就是这样",
          desc: "川建国同志"
        },
        {
          id: "10",
          url: require("../assets/home/dsj3.jpg"),
          title: "生活就是这样",
          desc: "川建国同志"
        }
      ]
    };
  },
  methods: {
    toDtl(id) {
      this.$router.push("/dtl?id=" + id);
    }
  }
};
</script>
<style lang='less' scoped>
.home_item {
  padding: 5px 12px;
  img {
    width: 100%;
  }
  .box {
    padding: 2px 2px 10px;
    .title {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 14px;
      font-weight: 600;
      color: #222;
    }
    .desc {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #999;
      font-size: 12px;
    }
  }
}
</style>


