<template>
  <div class="home pb50">
    <Search @Search="search" />
    <!-- 头部tab -->
    <van-tabs scrollspy v-model="active" sticky :offset-top="54">
      <van-tab title="推荐">
        <!-- banner -->
        <Banner />
        <!-- app -->
        <Toapp />
        <!-- 推荐list -->
        <Title text="推荐" name="查看更多" type="1"></Title>
        <Col12 />
      </van-tab>
      <van-tab title="热点">
        <!-- 热点list -->
        <Title text="热点" name="查看更多" type="1"></Title>
        <Col12 />
      </van-tab>
      <van-tab title="电视剧">
        <!-- 电视剧list -->
        <Title text="电视剧" name="查看更多" type="2"></Title>
        <Col8 />
      </van-tab>

      <van-tab title="电影">
        <!-- 电影list -->
        <Title text="电影" name="查看更多" type="3"></Title>
        <Col8 />
      </van-tab>
      <van-tab title="综艺">
        <!-- 综艺list -->
        <Title text="综艺" name="查看更多" type="5"></Title>
        <Col8 />
      </van-tab>
      <van-tab title="动漫">
        <!-- 动漫list -->
        <Title text="动漫" name="查看更多" type="6"></Title>
        <Col8 />
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import Title from "./components/title";
import Search from "../../components/search";
import Banner from "./components/Banner";
import Toapp from "./components/toApp";
import Col12 from "../../components/col12";
import Col8 from "../../components/col8";
export default {
  components: {
    Title,
    Search,
    Banner,
    Toapp,
    Col12,
    Col8
  },
  data() {
    return {
      // tuijianIcon: require("../../assets/home/sort.png"),
      active: 0
    };
  },
  methods: {
    toDtl(id) {
      this.$router.push("/dtl?id=" + id);
    },
    search(val) {
      console.log(val);
      this.$toast("搜索");
    }
  },
  created() {}
};
</script>

<style scoped lang="less">
.van-search__action {
  color: #fff;
}

/deep/.van-tabs__nav--complete {
  background: linear-gradient(
    0deg,
    rgba(240, 235, 235, 1),
    rgba(255, 255, 255, 1)
  );
}
/deep/.van-sticky--fixed {
  box-shadow: #999 1px 1px 10px 1px;
}
/deep/.van-tabs__line {
  bottom: 20px;
}
.home {
  padding-top: 54px;
}
</style>



