<template>
  <div class="navFixed">
    <van-nav-bar :title="meta.title" @click-left="$router.go(-1)">
      <template #left v-if="meta.isShow">
        <van-icon name="arrow-left" size="18" color="#333" />
      </template>
      <template #right v-if="isShare">
        <img
          @click="toShare"
          style="width:18px; margin-top:5px"
          src="../assets/shortVideo/share.png"
          alt
        />
      </template>
    </van-nav-bar>
    <van-share-sheet title="立即分享" v-model="showShare" :options="options" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      meta: {
        title: "ColdChain",
        isShow: true
      },
      isShare: false,
      showShare: false,
      options: [
        {
          name: "生成海报",
          icon: "https://img.yzcdn.cn/vant/custom-icon-fire.png"
        },
        {
          name: "二维码",
          icon: "https://img.yzcdn.cn/vant/custom-icon-light.png"
        },
        {
          name: "好友",
          icon: "https://img.yzcdn.cn/vant/custom-icon-water.png"
        }
      ]
    };
  },
  watch: {
    $route(to, from) {
      this.meta = to.meta;
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (to.name == "dtl") {
        vm.isShare = true;
      }
    });
  },

  methods: {
    toShare() {
      this.showShare = true;
    }
  },
  created() {
    // let hash = location.hash;
    // if (hash.indexOf("/dtl") > 0) {
    //   this.isShare = true;
    // }
    this.meta = this.$route.meta;
  }
};
</script>

<style scoped lang="less">
.navFixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  border-bottom: 2px solid #eee;
}
/deep/ .van-nav-bar__left {
  color: #333;
}

/deep/ .van-nav-bar__title {
  font-weight: bold;
  font-size: 18px;
}
</style>



