    font-size: 50px;
    margin-left: -25px;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    border-radius: 50%;
  }
}
.box {
  padding: 2px 2px 10px;
  .title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 14px;
    font-weight: 600;
