<template>
  <div class="content pt46">
    <div class="header_logo">
      <img src="../../static/jianguo.png" alt="logo" />
    </div>
    <div class="login_form">
      <van-field v-model="form.phone" clearable placeholder="请输入账号" />
      <van-field
        v-model="form.password"
        clearable
        :type="isPsd ? 'type' : 'password'"
        :right-icon="isShow ? 'closed-eye' : 'eye-o'"
        @click-right-icon="checkPsw"
        placeholder="请输入密码"
      />
      <van-field
        :type="isPsd ? 'type' : 'password'"
        v-model="form.password2"
        clearable
        placeholder="再次请输入密码"
      />
      <van-button type="primary" size="normal" @click="login">注册并登录</van-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        phone: "",
        password: "",
        password2: ""
      },
      isShow: false,
      isPsd: false
    };
  },
  methods: {
    // 密码查看
    checkPsw() {
      this.isShow = !this.isShow;
      this.isPsd = !this.isPsd;
    },

    login() {
      this.$toast.loading({
        duration: 0,
        message: "加载中...",
        loadingType: "spinner",
        forbidClick: true
      });
      setTimeout(() => {
        this.$toast.clear();
        let userInfo = {
          count: this.form.phone,
          tel: this.form.password,
          token: "123456"
        };
        localStorage.setItem("userInfo", JSON.stringify(userInfo));
        this.$store.dispatch("setUserInfo", userInfo);
        this.$toast.success("登录成功");
        this.$router.push("/home");
      }, 1000);
    },
    toLink(link) {
      this.$router.push(link);
    }
  },
  created() {}
};
</script>

<style scoped lang="less">
.content {
  .bck_img {
    position: fixed;
    left: 28px;
    right: 28px;
    bottom: 0;
    height: 90px;
    background-size: 100%;
  }
}
.header_logo {
  width: 80px;
  height: 80px;
  margin: 30px auto;
  img {
    width: 100%;
  }
}
.login_form {
  padding: 0 20px;
  .van-cell {
    background: rgba(247, 247, 247, 1);
    margin-bottom: 16px;
    border-radius: 4px;
  }
  .codeView {
    overflow: hidden;
    .ycode {
      width: 214px;
      float: left;
    }
    img {
      width: 100px;
      height: 43px;
      float: right;
    }
  }
  .van-button--normal {
    width: 100%;
    background: rgba(220, 47, 47, 1);
    border-radius: 4px;
    border: none;
    margin-top: 8px;
  }
}
.register {
  width: 100%;
  padding: 16px 20px;
  font-size: 14px;
  overflow: hidden;
  box-sizing: border-box;
  span:nth-child(1) {
    color: rgb(2, 1, 1);
    float: left;
  }
  span:nth-child(2) {
    float: right;
    color: rgba(200, 47, 47, 1);
  }
}
</style>



