<template>
  <div class="plr12 h40 video_list">
    <div class="list_title overflow">
      <img class="mr5 fl" :src="icon" alt />
    </div>
    <div class="text">{{ text }}</div>
    <div class="overflow" @click="toMore(type)">
      <div class="fl text">{{ name }}</div>
      <van-icon class="fl" name="arrow" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      default: require("../../../assets/home/sort.png")
    },
    name: {
      type: String,
      default: "查看更多"
    },
    type: [String, Number]
  },
  data() {
    return {};
  },
  methods: {
    toMore(type) {
      this.$router.push("/moreList?type=" + type);
    }
  }
};
</script>
<style lang="less" scoped>
.van-icon-arrow {
  margin-top: 2px;
}
.text {
  flex-grow: 1;
  text-align: left;
  font-weight: 600;
  font-size: 14px;
}
.video_list {
  display: flex;
  align-items: center;
  .size {
    font-weight: 600;
    font-size: 14px;
  }
}
.list_title {
  img {
    width: 30px;
    height: 30px;
  }
}
</style>



